import { html , render , WebComponent , customElement , attr , attrState , state, css, ViewTemplate, ViewContext, asyncAppend, repeat, children } from '@lithium-framework/core';
import '@lithium-framework/router-element';
import 'unofficial-pf-v5-wc';
import 'unofficial-pf-v5-wc-icons';
import '../components';

@customElement({
   name: "main-application",
   template : html`${(main: Main) => {
      return html`
        <div id="application">
            <div class="header">
               <horizontal-navbar></horizontal-navbar>
            </div>
            <div class="right-content">
               <vertical-navbar></vertical-navbar>
               <div class="content">
                  <div class="accueil">
                     <h1>Accueil</h1>
                  </div>
               </div>
            </div>
        </div>
      `
   }}`,
   styles: [
      css`
      #application{
         min-height: 100vh;
         background-color: var(--base-clr);
         color: var(--text-clr);
         display: grid;
         grid-template-rows: 10vh 1fr;
      }
      .content{
         margin-left: 10px;
         margin-top: 10px;
      }
      .right-content{
         display: grid;
         grid-template-columns: 250px 1fr;
         transition: all 0.3s ease;
      }
      .right-content.close{
         grid-template-columns: 84px 1fr;
      }
      `
   ]
})
export class Main extends WebComponent{
   @state() open: "true" | "false" | null = null;
   @attr() isOpen: boolean = true;

   connectedCallback() {
      super.connectedCallback();
      console.log('VerticalNavBar connecté');

      // Initialisation de l'état de la sidebar
      this.open = this.isOpen ? "true" : "false";

      // Écoute l'événement 'close-navbars' sur le document
      document.addEventListener('close-navbars', this.handleCloseContent.bind(this));
   }

   handleCloseContent(event) {
      console.log('L\'événement close-navbars a été détecté.', event);
      const rightContent = this.shadowRoot?.querySelectorAll('.right-content')?.[0];

      rightContent.classList.toggle('close')

      
      // Logique pour fermer la sidebar
      this.open = "false"; // Ferme la barre en changeant l'état

   }

   attributeChangedCallback(name, oldValue, newValue) {
      if (name === 'isopen') {
         console.log(`Attribut isOpen modifié de ${oldValue} à ${newValue}`);
         this.open = newValue ? "true" : "false";
         this.renderNavBar();
      }
   }

   renderNavBar() {
      console.log('Rendu de la barre de navigation avec open =', this.open);
   }
}


render(html`<main-application></main-application>`, document.body);